body {
  font-family: Yantramanav, sans-serif;
  background-color: #181c14;
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100dvh;
}

button {
  cursor: pointer;
  transition: filter 0.1s ease-in-out;
}

button:active {
  filter: brightness(1.2);
}

#main-container {
  display: grid;
  height: 100%;
  width: 100%;
  max-height: 500px;
  max-width: 1000px;
  padding: 16px 25px;
  align-items: center;
  grid-template-columns: 1fr 25%;
  grid-template-rows: 0.25fr 2.5fr 1fr 0.75fr;
  grid-template-areas:
