  display: grid;
  grid-template-rows: 1fr 2fr;
  align-items: center;
}

#delay-countdown-display {
  font-size: 2rem;
}

#countdown-display {
  line-height: 5rem;
  font-size: 5rem;
}

.timer-btns {
  grid-area: controls;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

.control-btns,
.set-timer-btns {
  border: none;
  color: #f5f5f5;
  background-color: #3c3d37;
  font-size: 1.1rem;
  padding: 0.25rem 0;
  width: 100px;
}

#pause {
  display: none;
}

#next {
  display: none;
}

.set-timer {
  grid-area: set-time;
  display: flex;
  flex-direction: column;
  gap: 30px;
  justify-self: end;
  margin-bottom: 50px;
}

.set-timer-btns {
  padding: 0.5rem 0;
  width: 150px;
}

.timer-counter {
  grid-area: counter;
}

#counter-container {
  display: flex;
  justify-content: end;
  align-items: center;
  gap: 30px;
}

#timer-counter-display {
  font-size: 0.75rem;
}

#total-time {
  font-size: 2rem;
}

#edit-timer-counter-btn {
  border: none;
  background: none;
  color: #f5f5f5;
  font-size: 1.75rem;
}

dialog::backdrop {
  background-color: #181c1480;
  backdrop-filter: blur(1px);
}

#set-timer-modal[open],
#edit-timer-counter-modal[open],
#counter-reset-modal[open] {
  color: #f5f5f5;
  background-color: #181c14;
  margin: auto;
  padding: 25px;
  gap: 10px;
  border: none;
  display: grid;
  justify-items: center;
  align-items: center;
}

.modal-button,
.counter-button,
.counter-reset-button {
  color: #f5f5f5;
