  background-color: #4f6d50;
}

#cancel,
#counter-modal-close-btn {
  justify-self: end;
  align-self: start;
  padding: 15px;
  width: 1rem;
  height: 1rem;
  background-color: #a04747;
}

/* Set Timer modal */
#set-timer-modal[open] {
  height: 100%;
  width: 100%;
  grid-template-columns: 3fr repeat(3, 1fr);
  grid-template-rows: 1fr repeat(4, 1fr) 0.5fr;
  grid-template-areas:
    'display close close close'
    'display button button button'
    'display button button button'
    'display button button button'
    'display button button button';
}

#set-timer-modal-display,
#delay-timer-modal-display {
  grid-area: display;
  font-size: 5rem;
}

#cancel {
  grid-area: close;
}

#warning {
  color: #b43f3f;
}

/* Timer counter modal */
#edit-timer-counter-modal[open] {
  height: 75%;
  width: 75%;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 0.5fr 1fr repeat(3, 1fr);
  grid-template-areas:
    'close close'
    'display display'
    'button button'
    'reset reset';
}

#modal-total-time-display {
  grid-area: display;
  font-size: 3.5rem;
}

.counter-button {
  width: 90%;
  font-size: 1.2rem;
}

#counter-modal-add-btn {
  justify-self: end;
}

#counter-modal-revert-btn {
  justify-self: start;
}

#counter-modal-reset-btn {
  grid-area: reset;
  width: 50%;
  background-color: #a04747;
}

#counter-modal-close-btn {
  grid-area: close;
}

/* Timer reset modal */
#counter-reset-modal[open] {
  height: 50%;
  width: 50%;
  background-color: #3c3d37;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 2fr 1fr 0.25fr;
}

#reset-confirmation-msg {
  font-size: 1.2rem;
  grid-column: 1 / span 2;
  text-align: center;
}

.counter-reset-button {
  background-color: #2b2b2b;
  font-size: 1rem;
}

/* Loader */
.loader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #181c14;
  transition: opacity 0.1s, visibility 0.1s;
}

.loader-hidden {
  opacity: 0;
  visibility: hidden;
}

.loader::after {
  content: '';
  width: 75px;
  height: 75px;
  border: 15px solid #3c3d37;
  border-top-color: #f5f5f5;
  border-radius: 50%;
  animation: loading 0.4s ease infinite;
}

@keyframes loading {
  from {
    transform: rotate(0turn);
  }
  to {
    transform: rotate(1turn);
  }
}

